import store from '@/store/store'
import * as showToaster from './show-toaster-mixin'
import VueInstance from '@/main'

const globalMixins = {
  methods: {
    ...showToaster, // mix the show toaster methods here
    camelCase (input) {
      return input.toLowerCase().replace(/_(.)/g, (match, group1) => {
        return group1.toUpperCase()
      })
    },
    showMenuItem (item) {
      if (store.getters.isAuthenticated) {
        return !item.requiresNotBeAuthenticated
      } else {
        return !item.requiresBeAuthenticated
      }
    },
    navigate (to) {
      if (to.external) {
        window.location = to.href
      } else {
        this.$router.push(to.href)
      }
    },
    confirmDialog (title, text, options) {
      let confirm = options || {}
      confirm.text = text
      confirm.title = title
      VueInstance.eventBus.$emit('triggerConfirm', confirm)

      return new Promise((resolve, reject) => {
        VueInstance.eventBus.$on('confirmAnswered', (answer) => {
          if (answer) {
            resolve(answer)
          } else {
            reject(answer)
          }
        })
      })
    }
  }
}

export default globalMixins



