import httpAdminAjax from '../../common/http-admin-ajax'
import appConfig from '../../config'

/* JWT AUTH api end point used to retrieve/send data */
const resetEndpoint = ''

export default {
  data: () => ({
    usernameOrEmail: ''
  }),
  computed: {
    userNameOrEmailRules () {
      return [
        v => !!v || this.$t('reset.usernameOrEmailRequired')
      ]
    }
  },
  methods: {
    goToLogin () {
      this.$router.push('/home')
    },
    submit () {
      let formData = new FormData()
      formData.set('action', appConfig.resetAction)
      formData.set('data', `${appConfig.resetUserLoginKey}=${this.usernameOrEmail}&${appConfig.passwordResetFormKey}=${appConfig.passwordResetFormId}`)

      httpAdminAjax.post(resetEndpoint, formData, {headers: { 'Content-Type': 'multipart/form-data' }})
      .then(response => {
        if (response.data && response.data !== 0 && response.data.status !== undefined) {
          if (response.data.status === false) {
            this.showError(this.$t('reset.invalidUserNameOrEmail'))
          } else {
            this.showSuccess(this.$t('reset.checkYourEmail'), {mode: 'multi-line'})
          }
        } else {
          this.showError(this.$t('reset.failWhileTryingToReset'))
        }
      })
      .catch(error => {
        this.showError(this.$t('reset.failWhileTryingToReset'))
        console.log(error)
      })
    }
  }
}



