import theme from '@/common/theme'
const topBorder = {
  bind (el, binding) {
    if (binding.arg === 'palette') {
      var palette = theme[binding.value]
      el.style = 'border-top: 5px solid ' + palette
    } else if (binding.arg === 'color') {
      el.style = 'border-top: 5px solid ' + binding.value
    } else {
      el.style = 'border-top: 5px solid ' + theme.primary
    }
  }
}

export default topBorder



