
export default {
  data () {
    return {
      drawer: true,
      clipped: false,
      atLogin: true,
      atSignUp: false,
      isLoggedIn: false,
      menuItems: []
    }
  },
  computed: {
    showLogin () {
      return !this.atLogin && !this.isLoggedIn
    },
    showSignUp () {
      return !this.atSignUp && !this.isLoggedIn
    }
  },
  methods: {
    toggleSidebar () {
      this.$store.commit('setLeftSideBarIsOpen', !this.$store.getters.leftSideBarOpen)
      // window.dispatchEvent(new Event('resize', {sideBarOpen: !this.$store.getters.leftSideBarOpen}))
    }
  },
  created () {
    this.$store.dispatch('fetchMainMenu').then(() => {
      this.menuItems = this.$store.getters.mainMenu
    })
  }
}



