import tokenService from './token-service'
import policyService from './policy-service'
import {CRUD, CRUDData} from '@/core/crud'
import LineChart from '@/fragments/charts/line-chart/line-chart'
import ChartWrapper from '@/fragments/charts/chart-wrapper/ChartWrapper'
import UsageParser from './token-usage-data-parser'
import VueInstance from '@/main'

export default {
  created () {
    // extend this component, adding CRUD functionalities and load the tokens
    let options = {
      queryOnStartup: true,
      savedMsg: this.$t('home_tokens.savedMsg'),
      confirmDestroyText: this.$t('home_tokens.confirmDestroyText'),
      destroyAbortedMsg: this.$t('home_tokens.destroyAbortedMsg'),
      resourceEmptyMsg: this.$t('home_tokens.tokenEmptyMsg')
    }
    CRUD(this, tokenService, options)

    // Get the available policies (type of tokens) and store on the data tokenType's key
    policyService.query().then((policies) => {
      this.tokenTypes = policies
    })
  },
  mounted () {
    let dates = getInitialDates()
    this.usageStartDate = dates.from
    this.usageEndDate = dates.to
    this.usageTodayDate = dates.to // initially, to is equal today
  },
  data: () => ({
    apiPolicy: null,
    newTokenName: null,
    tokenTypes: [],
    inspectingToken: null,
    usageLabels: [],
    showTokenUsage: false,
    usageDatasets: [],
    usageStartDate: null,
    usageEndDate: null,
    usageTodayDate: null,
    pagination: {
      sortBy: 'token_name'
    },
    ...CRUDData // create the crud data objects (resource, resources and modelService)
  }),
  methods: {
    seeUsage (item) {
      if (item.quota.quota_remaining === item.quota.quota_max) {
        this.showInfo(this.$t('home_tokens.tokenNotUsedYet'))
      } else if (!item.is_valid) {
        this.showInfo(this.$t('home_tokens.tokenIsNotValidToShowUsage'))
      } else {
        this.inspectingToken = item
        this.showTokenUsage = true
        buildChart(this)
      }
    },
    onUsageDateRangeChange () {
      buildChart(this)
    },
    changeSort (column) {
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending
      } else {
        this.pagination.sortBy = column
        this.pagination.descending = false
      }
      this.resources = this.lodash.orderBy(this.resources, column, ['desc'])
    }
  },
  computed: {
    emptyTableMessage () {
      return this.crudReady ? this.$t('home_tokens.noTokensToList') : this.$t('home_tokens.theTokensAreBeingLoaded')
    }
  },
  components: {
    LineChart,
    ChartWrapper
  }
}

function getInitialDates () {
  let dates = {}
  let now = new Date()
  let nextWeek = new Date()
  nextWeek.setDate(now.getDate() - 7)
  dates.from = [nextWeek.getFullYear(), leadingZero(nextWeek.getMonth() + 1), leadingZero(nextWeek.getDate())].join('-')
  dates.to = [now.getFullYear(), leadingZero(now.getMonth() + 1), leadingZero(now.getDate())].join('-')
  return dates
}

function leadingZero (nr) {
  return ('0' + nr).slice(-2)
}

function buildChart (context) {
  if (context.showTokenUsage && context.usageStartDate && context.usageEndDate) {
    let usageEndpoint = tokenService.getEndPoint(`${context.inspectingToken.hash}/usage`)
    let filters = {from: context.usageStartDate, to: context.usageEndDate}
    context.showInfo(context.$t('home_tokens.loadingUsageData'), {timeout: 2000})

    tokenService.customQuery(usageEndpoint, filters, {raw: true}).then((usageRawData) => {
      let parsed = UsageParser.parseData(usageRawData)
      context.usageLabels = parsed.labels
      context.usageDatasets = parsed.datasets
    })
  }
}



