
export default {
  props: {
    item:
    {
      type: Object,
      required: true
    },
    showIcon: {
      type: Boolean,
      required: false,
      default: false
    },
    showMenuFn: {
      type: Function,
      required: false
    },
    navigateFn: {
      type: Function,
      required: true
    }
  },
  methods: {
    showMenu (item) {
      if (this.showMenuFn) {
        return this.showMenuFn(item)
      } else {
        return true
      }
    }
  }
}



