/* JWT AUTH api end point used to retrieve/send data */
const authEndpoint = 'jwt-auth/v1/token'

export default {
  data: () => ({
    username: '',
    password: '',
    passVisibility: true
  }),
  computed: {
    userNameRules () {
      return [
        v => !!v || this.$t('login.usernameRequired')
      ]
    },
    passwordRules () {
      return [
        v => !!v || this.$t('login.passwordRequired')
      ]
    }
  },
  methods: {
    goToReset () {
      this.$router.push('/reset-password')
    },
    submit () {
      if (this.$refs.form.validate()) {
        var authData = {
          username: this.username,
          password: this.password
        }

        this.$http.post(authEndpoint, authData)
        .then(userData => {
          if (userData.data.token) {
            setUserAndRedirect(this, userData)
          } else {
            this.showError(this.$t('login.invalidCredentials'))
          }
        })
        .catch(error => {
          this.showError(this.$t('login.failWhileTryingToLogin'))
          console.log(error)
        })
      } else {
        this.showError(this.$t('login.invalidCredentials'))
      }
    }
  }
}

/**
 * Set user data and redirect to home
 * @param {*} context
 * @param {*} userData
 */
function setUserAndRedirect (context, userData) {
  userData = parseUserData(userData)
  context.$store.dispatch('login', userData)
  .then((user) => {
    context.$router.replace('/home')
  }).catch(error => {
    console.log('login error', error)
  })
}

/**
 * Parse a returned user data object to a local expected format
 *
 * @param {*} userData
 */
function parseUserData (userData) {
  // original user data from api
  userData = userData.data

  // set the default display name
  userData.displayName = userData.user_display_name

  // if display name is empty, tries to get from user nice name
  if (!userData.displayName) {
    userData.displayName = userData.user_nicename
  }

  // in the end, if still empty, use the user's email as display name
  if (!userData.displayName) {
    userData.displayName = userData.user_email
  }

  // set the user email in the appropriate/expected local attribute
  userData.userEmail = userData.user_email

  // set the user id
  userData.id = userData.id

  // return user data
  return userData
}



