import VueRecaptcha from 'vue-recaptcha'

export default {
  data () {
    return {
      verifiedCaptcha: false,
      user: {},
      passVisibility: true,
      sectors: [
        { text: this.$t('signup.sectors.logistics'), value: 0 },
        { text: this.$t('signup.sectors.realState'), value: 1 },
        { text: this.$t('signup.sectors.navigation'), value: 2 },
        { text: this.$t('signup.sectors.outdoorApps'), value: 3 },
        { text: this.$t('signup.sectors.energy'), value: 4 },
        { text: this.$t('signup.sectors.businessConsulting'), value: 5 },
        { text: this.$t('signup.sectors.locationMarket'), value: 6 },
        { text: this.$t('signup.sectors.insurance'), value: 7 },
        { text: this.$t('signup.sectors.education'), value: 8 },
        { text: this.$t('signup.sectors.publicSector'), value: 9 },
        { text: this.$t('signup.sectors.other'), value: 10 }
      ],
      gitHubSignupUrl: 'https://openrouteservice.org/log-in/?pp_social_login=github&amp;pp_current_url=https%3A%2F%2Fopenrouteservice.org%2Fsign-up%2F'
    }
  },
  computed: {
    userNameRules () {
      return [
        v => !!v || this.$t('signup.usernameRequired')
      ]
    },
    passwordRules () {
      return [
        v => !!v || this.$t('signup.passwordRequired')
      ]
    }
  },
  methods: {
    submit () {
      if (this.$refs.form.validate()) {
        this.$refs.recaptcha.execute()
        var authData = {
          username: this.username,
          password: this.password
        }

        this.$http.post('signup', authData)
        .then(userData => {

        })
        .catch(error => {
          this.showError(this.$t('signup.failWhileTryingToRegister'))
          console.log(error)
        })
      } else {
        this.showError(this.$t('signup.invalidData'))
      }
    },
    onCaptchaVerified (recaptchaToken) {
      this.user.recaptchaToken = recaptchaToken
      const self = this
      self.$refs.recaptcha.reset()
      self.verifiedCaptcha = true
    },
    onCaptchaExpired () {
      this.$refs.recaptcha.reset()
      this.verifiedCaptcha = false
    }
  },
  components: {
    VueRecaptcha
  }
}



