import Reset from '@/pages/reset/Reset'
import store from '../../store/store'

export default {
  path: '/reset-password',
  name: 'Reset',
  component: Reset,
  beforeEnter: (to, from, next) => {
    if (store.getters.isAuthenticated) {
      next('/home')
    } else {
      next()
    }
  }
}



