
import theme from '@/common/theme'

export default {
  props: ['customClass', 'headerBg', 'topBorderPalette', 'topBgColor', 'right'],
  computed: {
    headerBackground () {
      var bg = this.headerBg ? this.headerBg : 'transparent'
      return bg
    },
    topBorderColor () {
      var color = theme['primary']
      if (this.topBorderPalette) {
        color = theme[this.topBorderPalette]
      } else if (this.topBgColor) {
        color = theme[this.topBgColor] ? theme[this.topBgColor] : this.topBgColor
      }
      return color
    },
    align () {
      return this.right ? {float: 'right'} : {}
    }
  }
}



