const state = {
  token: null,
  displayName: null,
  userEmail: null,
  id: null
}

const getters = {
  user (state) {
    return {
      token: state.token,
      userEmail: state.userEmail,
      displayName: state.displayName,
      id: state.id
    }
  },
  isAuthenticated (state) {
    return state.token != null && validToken() !== false
  }
}

const mutations = {
  authUser: (state, userData) => {
    state.token = userData.token
    state.userEmail = userData.userEmail
    state.displayName = userData.displayName
    state.id = userData.id
  },
  clearAuthData: (state) => {
    state.token = null
    state.displayName = null
    state.userEmail = null
    state.id = null
  }
}

const actions = {
  setLogoutTimer ({commit}, expirationTime) {
    setTimeout(() => {
      commit('clearAuthData')
    }, expirationTime * 1000)
  },
  login ({commit, dispatch}, authData) {
    return new Promise((resolve, reject) => {
      const now = new Date()
      const expirationDate = new Date(now.getTime() + 3200 * 1000)

      localStorage.setItem('expiresAt', expirationDate)
      localStorage.setItem('token', authData.token)
      localStorage.setItem('displayName', authData.displayName)
      localStorage.setItem('userEmail', authData.userEmail)
      localStorage.setItem('userId', authData.id)

      commit('authUser', authData)
      dispatch('setLogoutTimer', 3200)
      resolve(authData)
    })
  },
  tryAutoLogin ({commit}) {
    return new Promise((resolve, reject) => {
      let token = validToken()
      if (token) {
        const userEmail = localStorage.getItem('userEmail')
        const displayName = localStorage.getItem('displayName')
        const id = localStorage.getItem('userId')
        commit('authUser', {
          token: token,
          displayName: displayName,
          userEmail: userEmail,
          id: id
        })
      }
      resolve()
    })
  },
  logout ({commit}) {
    return new Promise((resolve, reject) => {
      commit('clearAuthData')
      localStorage.removeItem('token')
      localStorage.removeItem('displayName')
      localStorage.removeItem('userEmail')
      localStorage.removeItem('userId')
      resolve()
    })
  }
}

/**
 * Validate the token from browser's local storage
 * To do so, it checks if the token exists and if has not expired
 */
function validToken () {
  const token = localStorage.getItem('token')
  const expiresAt = new Date(localStorage.getItem('expiresAt'))
  const now = new Date()
  if (token && expiresAt > now) {
    return token
  }
  return false
}

export default {
  state,
  getters,
  mutations,
  actions
}



