import Header from '@/fragments/header/Header'
import Sidebar from '@/fragments/sidebar/Sidebar'
import Footer from '@/fragments/footer/Footer'
import Toaster from '@/fragments/toaster/Toaster'
import Welcome from '@/fragments/welcome/Welcome'
import Confirm from '@/fragments/dialogs/confirm/Confirm'

export default {
  data () {
    return {
      items: [{
        icon: 'bubble_chart',
        title: 'Inspire'
      }],
      miniVariant: false,
      fixed: false,
      title: 'OSM dev dashboard',
      atSignIn: true,
      atSignup: false,
      isLoggedIn: false,
      showLoading: false
    }
  },
  computed: {
    showSingIn () {
      return !this.atSignIn && !this.isLoggedIn
    },
    showSingUp () {
      return !this.atSignup && !this.isLoggedIn
    }
  },
  name: 'App',
  components: {
    appHeader: Header,
    appSidebar: Sidebar,
    appFooter: Footer,
    appToaster: Toaster,
    appWelcome: Welcome,
    appConfirm: Confirm
  },
  created () {
    this.eventBus.$on('showLoading', (value) => {
      this.showLoading = value
    })
  }
}



