import userService from './user-service'
import {CRUD, CRUDData} from '@/core/crud'

export default {
  created () {
    // extend this component, adding CRUD functionalities and load the tokens
    let options = {
      queryOnStartup: false,
      updatedMsg: 'Profile updated'
    }
    CRUD(this, userService, options)
    // 3475
    userService.get(this.$store.getters.user.id).then((user) => {
      this.resource = user

      // As we have set up queryOnStartup as false
      // we have to define manually when the form is ready for crud
      // this is used in conjunction with v-if="crudReady" to avoid
      // render errors while the resource or crud methods are not
      // ready yet. So the form will be rendered only when we say it is ready
      // this property/data is defined in the CRUDData and injected using  ...CRUDData
      this.crudReady = true
    })
  },
  data () {
    return {
      ...CRUDData, // adds: resource, resources, crudReady
      passVisibility: true,
      sectors: [
        { text: this.$t('signup.sectors.logistics'), value: 'Logistics' },
        { text: this.$t('signup.sectors.realState'), value: 'Real estate' },
        { text: this.$t('signup.sectors.navigation'), value: 'Navigation' },
        { text: this.$t('signup.sectors.outdoorApps'), value: 'Outdoor Apps' },
        { text: this.$t('signup.sectors.energy'), value: 'Energy' },
        { text: this.$t('signup.sectors.businessConsulting'), value: 'Business Consulting' },
        { text: this.$t('signup.sectors.locationMarket'), value: 'Location Market' },
        { text: this.$t('signup.sectors.insurance'), value: 'Insurance' },
        { text: this.$t('signup.sectors.education'), value: 'Education' },
        { text: this.$t('signup.sectors.publicSector'), value: 'Public Sector' },
        { text: this.$t('signup.sectors.other'), value: 'Other' }
      ]
    }
  },
  computed: {
    userNameRules () {
      return [
        v => !!v || this.$t('profile.usernameRequired')
      ]
    },
    passwordRules () {
      return [
        (this.resource.password === undefined || this.resource.password === this.resource.confirmPassword) || this.$t('profile.passwordMatch')
      ]
    },
    emailRules () {
      const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      return [
        v => (!!v && pattern.test(v)) || this.$t('profile.pleaseTypeAValidEmail')
      ]
    },
    websiteRules () {
      const pattern = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/
      return [
        v => ((v === undefined || v === '') || pattern.test(v)) || this.$t('profile.pleaseTypeAValidWebsite')
      ]
    }
  },
  methods: {
    removeSector (item) {
      this.resource.metas.ors_usage.splice(this.resource.metas.ors_usage.indexOf(item), 1)
      this.resource.metas.ors_usage = [...this.resource.metas.ors_usage]
    }
  }
}



